<template>
  <table class="table table-borderless bg-light rounded">
    <tbody v-if="summary === null">
      <tr>
        <td colspan="2">
          <!--Preloader-->
          <preloader-component />
        </td>
      </tr>
    </tbody>
    <tbody v-else>
      <tr>
        <th scope="row" class="text-right" width="55%">নতুন আক্রান্ত:</th>
        <td>{{ replaceNumbers(summary.newInfected) }}</td>
      </tr>
      <tr>
        <th scope="row" class="text-right" width="55%">মোট আক্রান্ত:</th>
        <td>{{ replaceNumbers(summary.totalInfected) }}</td>
      </tr>
      <tr>
        <th scope="row" class="text-right" width="55%">মোট সুস্থ্য:</th>
        <td>{{ replaceNumbers(summary.totalWell) }}</td>
      </tr>
      <tr>
        <th scope="row" class="text-right" width="55%">মোট মৃত্যু:</th>
        <td>{{ replaceNumbers(summary.totalDead) }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { mapState, mapActions } from "vuex";
import PreloaderComponent from "../common/PreloaderComponent";
export default {
  name: "SummaryTableComponent",
  components: { PreloaderComponent },
  computed: {
    ...mapState(["summary"])
  },
  methods: {
    ...mapActions(["bindSummary"]),
    loadData() {
      if (this.summary === null) {
        this.bindSummary();
      }
    },
    replaceNumbers(input) {
      return input
        .toString()
        .replace(/0/g, "০")
        .replace(/1/g, "১")
        .replace(/2/g, "২")
        .replace(/3/g, "৩")
        .replace(/4/g, "৪")
        .replace(/5/g, "৫")
        .replace(/6/g, "৬")
        .replace(/7/g, "৭")
        .replace(/8/g, "৮")
        .replace(/9/g, "৯");
    }
  },
  mounted() {
    this.loadData();
  }
};
</script>

<style scoped></style>
