<template>
  <chart-js-component
    :labels="labels"
    :data-set="dataSet"
    :chart-title="chartTitle"
    chart-type="pie"
    :height="200"
  />
</template>

<script>
import { mapState } from "vuex";
import ChartJsComponent from "../common/ChartJsComponent";
export default {
  name: "OccupationWiseGraphComponent",
  components: { ChartJsComponent },
  computed: {
    ...mapState(["occupations", "patients"])
  },
  watch: {
    occupations: {
      handler: "updateOccupations",
      deep: true,
      immediate: true
    },
    patients: {
      handler: "updatePatients",
      deep: true,
      immediate: true
    }
  },
  data: () => ({
    labels: [],
    dataSet: [],
    chartTitle: "পেশা ভিত্তিক পরিসংখ্যান"
  }),
  methods: {
    updateOccupations(_occupations) {
      if (Array.isArray(_occupations)) {
        const __occupations = _occupations.map(_occupation => {
          return _occupation.name;
        });

        if (__occupations.length > 0) {
          this.labels = __occupations;
        }
      }
    },
    updatePatients(_patients) {
      const _output = [];
      this.labels.forEach(label => {
        const currentOccupation = this.occupations.find(
          occupation => occupation.name === label
        ).id;

        const occupationWisePatients = _patients.filter(
          patient => patient.occupationId === currentOccupation
        );
        _output.push(occupationWisePatients.length);
      });

      // background color
      const _backgroundColor = [];
      // border color
      const _borderColor = [];
      _output.forEach(() => {
        const r = this.getRandomInt(10, 200);
        const g = this.getRandomInt(10, 200);
        const b = this.getRandomInt(10, 200);

        // generate background color
        _backgroundColor.push(`rgba(${r}, ${g}, ${b}, .6)`);
        // generate border color
        _borderColor.push(`rgba(${r}, ${g}, ${b}, 1)`);
      });

      this.dataSet = [
        {
          label: "আক্রান্তের সংখ্যা",
          data: _output,
          backgroundColor: _backgroundColor,
          borderColor: _borderColor,
          borderWidth: 1
        }
      ];
    },
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    }
  }
};
</script>

