<template>
  <!--<div class="table-responsive-sm">
    <table class="table table-sm table-hover table-bordered text-center">
      <thead>
        <tr>
          <th scope="col">নং</th>
          <th scope="col">শনাক্তের তারিখ</th>
          <th scope="col">নাম</th>
          <th scope="col">পেশা</th>
          <th scope="col">ঠিকানা</th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="patients === null">
          <td colspan="5">
            <preloader-component />
          </td>
        </tr>
        <tr v-else-if="patients.length === 0">
          <td colspan="5" class="text-center">
            No patient is available
          </td>
        </tr>
        <tr v-for="(patient, index) in patients" :key="index">
          <th scope="row">{{ replaceNumbers((patients.length === 1) ? 1 : (patients.length - index) ) }}</th>
          <td>
            {{
              patient.infectedDate.toDate().toLocaleDateString("bn-BD", {
                year: "numeric",
                month: "long",
                day: "numeric"
              })
            }}
          </td>
          <td>{{ patient.name }}</td>
          <td>
            {{ getterSpecificOccupationById(patient.occupationId).name }}
          </td>
          <td>
            {{ patient.address }},
            {{ getterSpecificUnionById(patient.unionId).name }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>-->
  <vue-good-table
    :columns="columns"
    :rows="generateTableRow"
    :sort-options="{
      initialSortBy: { field: 'serial', type: 'desc' }
    }"
    :search-options="{
      enabled: true,
      skipDiacritics: true,
      placeholder: 'রোগীর নাম লিখুন',
      searchFn: tableSearchOptions
    }"
    :pagination-options="{
      enabled: true,
      mode: 'records',
      perPage: 25,
      perPageDropdown: [25, 50, 75, 100],
      dropdownAllowAll: true,
      setCurrentPage: 1,
      nextLabel: 'Next',
      prevLabel: 'Previous',
      rowsPerPageLabel: 'Patients per page',
      ofLabel: 'of',
      pageLabel: 'page', // for 'pages' mode
      allLabel: 'All'
    }"
  >
    <template slot="table-row" slot-scope="props">
      <span v-if="props.column.field === 'serial'">
        <span>{{ replaceNumbers(props.row.serial) }}</span>
      </span>
      <span v-else-if="props.column.field === 'date'">
        <span>{{ formatToLocaleDate(props.row.date) }}</span>
      </span>
      <span v-else>
        {{ props.formattedRow[props.column.field] }}
      </span>
    </template>
    <div slot="emptystate" class="text-center">
      <p>No patients available</p>
      <!--<preloader-component />-->
    </div>
  </vue-good-table>
</template>

<script>
/*import PreloaderComponent from "../common/PreloaderComponent";*/
import { mapState, mapActions, mapGetters } from "vuex";
// import the styles
import "vue-good-table/dist/vue-good-table.css";
import { VueGoodTable } from "vue-good-table";
export default {
  name: "PatientTableComponent",
  components: { /*PreloaderComponent,*/ VueGoodTable },
  computed: {
    ...mapState(["unions", "occupations", "patients"]),
    ...mapGetters(["getterSpecificUnionById", "getterSpecificOccupationById"]),
    generateTableRow() {
      const row = [];

      if (Array.isArray(this.patients)) {
        this.patients.forEach((patient, index) => {
          row.push({
            serial:
              this.patients.length === 1 ? 1 : this.patients.length - index,
            date: patient.infectedDate.seconds,
            name: patient.name,
            occupation: this.getterSpecificOccupationById(patient.occupationId)
              .name,
            address: `${patient.address}, ${
              this.getterSpecificUnionById(patient.unionId).name
            }`
          });
        });
      }

      return row;
    }
  },
  data() {
    return {
      columns: [
        {
          label: "নং",
          field: "serial",
          type: "number",
          tdClass: "text-center",
          thClass: "text-center"
        },
        {
          label: "শনাক্তের তারিখ",
          field: "date",
          type: "number",
          tdClass: "text-center",
          thClass: "text-center"
        },
        {
          label: "নাম",
          field: "name",
          tdClass: "text-center",
          thClass: "text-center"
        },
        {
          label: "পেশা",
          field: "occupation",
          tdClass: "text-center",
          thClass: "text-center"
        },
        {
          label: "ঠিকানা",
          field: "address",
          tdClass: "text-center",
          thClass: "text-center"
        }
      ]
    };
  },
  methods: {
    ...mapActions(["bindUnions", "bindPatients", "bindOccupations"]),
    loadData() {
      // load unions if null
      if (this.unions === null) {
        this.bindUnions();
      }
      // load occupations if null
      if (this.occupations === null) {
        this.bindOccupations();
      }
      // load patients if null
      if (this.patients === null) {
        this.bindPatients();
      }
    },
    tableSearchOptions(row, col, cellValue, searchTerm) {
      // search in name column and search term match with cell value
      return col.field === "name" && cellValue.includes(searchTerm);
    },
    formatToLocaleDate(seconds) {
      return new Date(seconds * 1000).toLocaleDateString("bn-BD", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
    },
    replaceNumbers(input) {
      return input
        .toString()
        .replace(/0/g, "০")
        .replace(/1/g, "১")
        .replace(/2/g, "২")
        .replace(/3/g, "৩")
        .replace(/4/g, "৪")
        .replace(/5/g, "৫")
        .replace(/6/g, "৬")
        .replace(/7/g, "৭")
        .replace(/8/g, "৮")
        .replace(/9/g, "৯");
    }
  },
  mounted() {
    this.loadData();
  }
};
</script>

<style scoped></style>
