<template>
  <chart-js-component
    :labels="labels"
    :data-set="dataSet"
    :height="130"
    :chart-title="chartTitle"
  />
</template>

<script>
import ChartJsComponent from "../common/ChartJsComponent";
import { mapState } from "vuex";
export default {
  name: "UnionWiseGraphComponent",
  components: { ChartJsComponent },
  computed: {
    ...mapState(["patients", "unions"])
  },
  watch: {
    unions: {
      handler: "updateUnions",
      immediate: true,
      deep: true
    },
    patients: {
      handler: "updatePatients",
      immediate: true,
      deep: true
    }
  },
  data: () => ({
    labels: [],
    dataSet: [],
    chartTitle: "ইউনিয়ন ভিত্তিক পরিসংখ্যান"
  }),
  methods: {
    updateUnions(_unions) {
      if (Array.isArray(_unions)) {
        const __unions = _unions.map(_union => {
          return _union.name;
        });

        if (__unions.length > 0) {
          this.labels = __unions;
        }
      }
    },
    updatePatients(_patients) {
      const _output = [];
      this.labels.forEach(label => {
        const currentUnion = this.unions.find(union => union.name === label).id;

        const unionWisePatients = _patients.filter(
          patient => patient.unionId === currentUnion
        );
        _output.push(unionWisePatients.length);
      });

      // background color
      const _backgroundColor = [];
      // border color
      const _borderColor = [];
      _output.forEach(() => {
        // generate background color
        _backgroundColor.push("rgba(255, 99, 132, 0.6)");
        // generate border color
        _borderColor.push("rgba(255, 99, 132, 1)");
      });

      this.dataSet = [
        {
          label: "আক্রান্তের সংখ্যা",
          data: _output,
          backgroundColor: _backgroundColor,
          borderColor: _borderColor,
          borderWidth: 1
        }
      ];
    },
    replaceNumbers(input) {
      return input
        .toString()
        .replace(/0/g, "০")
        .replace(/1/g, "১")
        .replace(/2/g, "২")
        .replace(/3/g, "৩")
        .replace(/4/g, "৪")
        .replace(/5/g, "৫")
        .replace(/6/g, "৬")
        .replace(/7/g, "৭")
        .replace(/8/g, "৮")
        .replace(/9/g, "৯");
    }
  },
  mounted() {}
};
</script>

<style scoped></style>
